* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  font-size: 62.5%;
}

body {
  width: 100vw;
}

.flex-container {
  display: flex;
}

header {
  height: 6rem;
  width: 100%;
  color: #23237d;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  position: fixed;
}

nav {
  gap: .5rem;
}

.fa-solid {
  background-color: #e4e4e4;
  border-radius: 5px;
  padding: 1rem;
  font-size: 1.3rem;
}

h1 {
  font-family: monospace;
  font-size: 3rem;
  font-style: italic;
}

main {
  padding-top: 6rem;
}

section:first-of-type {
  height: 25rem;
  width: 100vw;
  background-image: url("../../../../../sweetBg.204225b3.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 10%;
}

div a {
  display: none;
}

.fa-bars {
  display: block;
}

h2 {
  width: 100%;
  color: #0f0f76;
  padding-bottom: 1.5rem;
  font-family: monospace;
  font-size: 3rem;
  font-weight: bold;
}

h2 span {
  color: #f09;
}

section p {
  width: 100%;
  text-align: left;
  color: #5b606a;
  padding-bottom: 2rem;
  font-family: monospace;
  font-size: 1rem;
  line-height: 1.5rem;
}

i:hover {
  background-color: #ff1493;
}

section button {
  background-color: #ff1493;
  border: 2px solid #0f0f76;
  border-radius: 5px;
  padding: .5rem 2rem;
  font-family: monospace;
  font-size: 1.5rem;
}

section button:hover {
  background-color: #7fffd4;
}

section:nth-of-type(2) {
  max-width: 100%;
  background-color: #ececec;
  flex-flow: wrap;
  justify-content: center;
  gap: 2rem;
  padding: 2rem 3rem;
}

section:nth-of-type(2) h2 {
  font-size: 2.5rem;
}

article {
  width: 250px;
  max-width: 400px;
  text-align: center;
  background-color: #dbfaf0;
  flex-grow: 1;
  padding: 10px 15px;
  box-shadow: 0 0 5px #b0b0b0;
}

article img {
  width: 150px;
  height: 150px;
  object-fit: cover;
}

@media (min-width: 800px) {
  section:first-of-type {
    height: 20rem;
    width: 100vw;
    background-image: url("../../../../../sweetBg.204225b3.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 4rem 20%;
  }

  .popupnav {
    flex-grow: 1;
    justify-content: flex-end;
    align-items: center;
    gap: 2rem;
    margin-right: 2rem;
    display: flex;
  }

  div a {
    color: #23237d;
    font-size: 1.5rem;
    text-decoration: none;
    display: block;
  }

  div a:hover {
    text-decoration: underline #ff1493;
    text-decoration-thickness: .2rem;
  }

  .fa-bars {
    display: none;
  }
}

