
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
:root {
    font-size: 62.5%;
}

body {
    width: 100vw;
}

.flex-container {
    display: flex;
}

header {
    height: 6rem;
    background-color: white;
    position: fixed;
    padding: 2rem;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    color: rgb(35, 35, 125);
}

nav {
    gap: 0.5rem;
}
.fa-solid {
    padding: 1rem;
    font-size: 1.3rem;
    border-radius: 5px;
    background-color: rgb(228, 228, 228);
}

h1 {
    font-style: italic;
    font-family: monospace;
    font-size: 3rem;
}

main {
padding-top: 6rem;
}

section:first-of-type {
    background-image: url(./images/sweetBg.jpg);
    background-repeat: no-repeat;
    background-size:cover;
    background-position:center;
    height: 25rem;
    padding: 10% 10%;
    width: 100vw;
}

div a {
    display: none;
}

.fa-bars {
    display: block;
}

h2 {
    width: 100%;
    font-weight: bold;
    font-size: 3rem;
    font-family: monospace;
    color: rgb(15, 15, 118);
    padding-bottom: 1.5rem;
    
}

h2 span {
    color: rgb(255, 0, 153);
}

section p {
    width: 100%;
    font-family: monospace;
    text-align: left;
    line-height: 1.5rem;
    font-size: 1rem;
    padding-bottom: 2rem;
    color: rgb(91, 96, 106);
}

i:hover {
    background-color: deeppink;
}

section button {
    border: 2px solid rgb(15, 15, 118);
    border-radius: 5px;
    font-size: 1.5rem;
    font-family: monospace;
    background-color: deeppink;
    padding: 0.5rem 2rem;
}

section button:hover {
    background-color: aquamarine;
}

section:nth-of-type(2) {
    max-width: 100%;
    padding: 2rem 3rem;
    background-color: rgb(236, 236, 236);
    flex-direction: row;
    justify-content: center;
    gap: 2rem;
    flex-wrap: wrap;
}

section:nth-of-type(2) h2 {
    font-size: 2.5rem;
}

article {
    flex-grow: 1;
    width: 250px;
    max-width: 400px;
    padding: 10px 15px;
    text-align: center;
    background-color: rgb(219, 250, 240);
    box-shadow: 0px 0px 5px 0px rgb(176, 176, 176);
}


article img {
    width: 150px;
    height: 150px;
    object-fit: cover;
}

@media (min-width: 800px) {
    section:first-of-type {
        background-image: url(./images/sweetBg.jpg);
        background-repeat: no-repeat;
        background-size:cover;
        background-position:center;
        height: 20rem;
        padding: 4rem 20%;
        width: 100vw;
   }



.popupnav {  
    margin-right: 2rem;  
    display: flex;
    justify-content:flex-end;
    align-items: center;
    flex-grow: 1;
    gap: 2rem;
}

div a{
    display: block;
    text-decoration: none;
    font-size: 1.5rem;
    color: rgb(35, 35, 125);
}

div a:hover {
    text-decoration: underline;
    text-decoration-color: deeppink;
    text-decoration-thickness: 0.2rem;
}

.fa-bars {
    display: none;
}
}